.lightrope {
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  position: fixed;
  top: 0;
  z-index: 1;
  margin: -15px 0 0 0;
  padding: 0;
  pointer-events: none;
  width: 100%;
}
.lightrope li {
  position: relative;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  list-style: none;
  margin: 0;
  padding: 0;
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: 20px;
  display: inline-block;
  background: lime;
  box-shadow: 0px 2px 24px 3px lime;
  -webkit-animation-name: flash-1;
  animation-name: flash-1;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
}
.lightrope li:nth-child(2n + 1) {
  background: blue;
  box-shadow: 0px 2px 24px 3px rgba(0, 0, 255, 0.5);
  -webkit-animation-name: flash-2;
  animation-name: flash-2;
  -webkit-animation-duration: 0.4s;
  animation-duration: 0.4s;
}
.lightrope li:nth-child(4n + 2) {
  background: #f70000;
  box-shadow: 0px 2px 24px 3px #f70000;
  -webkit-animation-name: flash-3;
  animation-name: flash-3;
  -webkit-animation-duration: 1.1s;
  animation-duration: 1.1s;
}
.lightrope li:nth-child(odd) {
  -webkit-animation-duration: 1.8s;
  animation-duration: 1.8s;
}
.lightrope li:nth-child(3n + 1) {
  -webkit-animation-duration: 1.4s;
  animation-duration: 1.4s;
}
.lightrope li:before {
  content: '';
  position: absolute;
  background: #222;
  width: 10px;
  height: 4px;
  border-radius: 3px;
  top: -2px;
  left: 1px;
}
.lightrope li:after {
  content: '';
  top: -6px;
  left: 9px;
  position: absolute;
  width: 52px;
  height: 8px;
  border-bottom: solid #222 2px;
  border-radius: 50%;
}
.lightrope li:last-child:after {
  content: none;
}
.lightrope li:first-child {
  margin-left: -40px;
}

@-webkit-keyframes flash-1 {
  0%,
  100% {
    background: lime;
    box-shadow: 0px 2px 24px 3px lime;
  }
  50% {
    background: rgba(0, 255, 0, 0.4);
    box-shadow: 0px 2px 24px 3px rgba(0, 255, 0, 0.2);
  }
}

@keyframes flash-1 {
  0%,
  100% {
    background: lime;
    box-shadow: 0px 2px 24px 3px lime;
  }
  50% {
    background: rgba(0, 255, 0, 0.4);
    box-shadow: 0px 2px 24px 3px rgba(0, 255, 0, 0.2);
  }
}
@-webkit-keyframes flash-2 {
  0%,
  100% {
    background: blue;
    box-shadow: 0px 2px 24px 3px blue;
  }
  50% {
    background: rgba(0, 0, 255, 0.4);
    box-shadow: 0px 2px 24px 3px rgba(0, 0, 255, 0.2);
  }
}
@keyframes flash-2 {
  0%,
  100% {
    background: blue;
    box-shadow: 0px 2px 24px 3px blue;
  }
  50% {
    background: rgba(0, 0, 255, 0.4);
    box-shadow: 0px 2px 24px 3px rgba(0, 0, 255, 0.2);
  }
}
@-webkit-keyframes flash-3 {
  0%,
  100% {
    background: #f70000;
    box-shadow: 0px 2px 24px 3px #f70000;
  }
  50% {
    background: rgba(247, 0, 0, 0.4);
    box-shadow: 0px 2px 24px 3px rgba(247, 0, 0, 0.2);
  }
}
@keyframes flash-3 {
  0%,
  100% {
    background: #f70000;
    box-shadow: 0px 2px 24px 3px #f70000;
  }
  50% {
    background: rgba(247, 0, 0, 0.4);
    box-shadow: 0px 2px 24px 3px rgba(247, 0, 0, 0.2);
  }
}

